import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import FileViewer from 'react-file-viewer';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import Divider from '@mui/material/Divider';
import TextField from '@mui/material/TextField';
import SendIcon from '@mui/icons-material/Send';





const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

export default function BasicModal({files=[], sendfileHandler, modelToggle, setModelToggle }) {

  const [valueTemp, setValueTemp] = React.useState('');
  const ref = React.useRef(null);

  const listRef = React.useRef(null);

  const onChangeHandler = (e)=>{
      const { value } = e.target
          setValueTemp(value)
  }
  const handleKeyDown =(e)=>{
      if(e.keyCode === 13 && e.shiftKey){
          return;
      }else if(e.keyCode === 13) {
          e.preventDefault();
          sendSocketHandler(valueTemp);
          setValueTemp('');
      }
  }
 
  return (
    <div>
      <Modal
        open={modelToggle}
        onClose={()=>setModelToggle(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
        <ImageList cols={3} >
          <ul>           { files && files.map(({ source, name, size, file })=>(
               <Box sx={{height: '%'}}>

                  <li><h6>{name}</h6></li>
                    {/* <FileViewer style={{height: '20%'}}
                fileType={name.split('.')[1]}
                filePath={source} 
                />  */}
                </Box>
                ))}
        </ul>
        </ImageList>
        <Box >
        <Paper sx={{ p: '2px 4px', display: 'flex', alignItems: 'center' }} elevation={3}>
               <IconButton color="primary" onClick={()=>setModelToggle(false)} sx={{ p: '10px' }} aria-label="directions">
                  cancel
                </IconButton>
                <Divider sx={{ height: 40, m: 1 }} orientation="vertical" />

                <TextField
                    id="outlined-multiline-flexible"
                    placeholder='Type your messge here...'
                    multiline
                    fullWidth
                    maxRows={4}
                    value={valueTemp}
                    onChange={onChangeHandler}
                    onKeyDown={handleKeyDown}
                />
                <Divider sx={{ height: 40, m: 1 }} orientation="vertical" />
                <IconButton color="primary" onClick={() => { sendfileHandler(valueTemp), setValueTemp('') }} sx={{ p: '10px' }} aria-label="directions">
                    <SendIcon />
                </IconButton>
            </Paper>
        {/* <Button onClick={()=>setModelToggle(false)} >Cancel</Button> */}
        {/* <Button onClick={()=>sendfileHandler(valueTemp)} >Send</Button> */}
        </Box>
        </Box>
      </Modal>
    </div>
  );
}
