import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Checkbox from '@mui/material/Checkbox';
import Avatar from '@mui/material/Avatar';
import SearchIcon from '@mui/icons-material/Search';
import InputBase from '@mui/material/InputBase';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import Box from '@mui/material/Box';
import Badge from '@mui/material/Badge';
import MailIcon from '@mui/icons-material/Mail';




export default function ChatSidebar({ messagesUser, listClickHandler, receiverId, searchInputHandler, searchData, searchDataClickHandler }) {
  const [messageUsers, setMessageUsers] = React.useState([]);



  return (
    <List dense sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>

      <Paper
        sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', borderRadius: 1 }}
      >
        <InputBase
          sx={{ ml: 1, flex: 1 }}
          placeholder="Search.."
          onChange={searchInputHandler}
          inputProps={{ 'aria-label': 'search google maps' }}
        />
        <IconButton sx={{ p: '10px' }} aria-label="search">
          <SearchIcon />
        </IconButton>
      </Paper>



      {searchData && <Box sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper', marginTop: 0.1 }}>
        <nav aria-label="search names">
          <List>
            {searchData.map((el) => (
              <ListItem key={el._id} sx={{ padding: 0 }}>
                <ListItemButton component="a" href="#" onClick={() => searchDataClickHandler(el._id)}>
                  <ListItemText primary={el.name} />
                </ListItemButton>
              </ListItem>
            ))
            }
          </List>
        </nav>
      </Box>
      }

      {messagesUser.map((value) => {
        const labelId = `checkbox-list-secondary-label-${value._id}`;

        return (
          <ListItem
            key={value._id}
            disablePadding
            onClick={() => listClickHandler(value._id)}
          >

            <ListItemButton sx={{ backgroundColor: receiverId === value._id ? 'mediumorchid' : '', borderRadius: 1 }} >
              <ListItemAvatar>
                <Avatar
                  alt={value.name}
                  src={`/static/images/avatar/${value._id + 1}.jpg`}
                />
              </ListItemAvatar>
              <ListItemText id={labelId} primary={`${value.name}`} />
              <Badge badgeContent={value.unseenMessages} color="secondary" />
            </ListItemButton>
          </ListItem>
        );
      })}
    </List>
  );
}