import * as React from 'react';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';

import AttachFileIcon from '@mui/icons-material/AttachFile';
import Paper from '@mui/material/Paper';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ListItemText from '@mui/material/ListItemText';
import Avatar from '@mui/material/Avatar';
import TextField from '@mui/material/TextField';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import SendIcon from '@mui/icons-material/Send';
import Typography from '@mui/material/Typography';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import DownloadIcon from '@mui/icons-material/Download';
import { Link } from 'react-router-dom'

import FileViewer from "react-file-viewer";


export default function ChatTextBox({ sendSocketHandler, messagesData, userId, selectFileHandler, downlodeHandler }) {
    const [valueTemp, setValueTemp] = React.useState('');
    const ref = React.useRef(null);

    const listRef = React.useRef(null);

    const onChangeHandler = (e)=>{
        const { value } = e.target
            setValueTemp(value)
    }
    const handleKeyDown =(e)=>{
        if(e.keyCode === 13 && e.shiftKey){
            return;
        }else if(e.keyCode === 13) {
            e.preventDefault();
            sendSocketHandler(valueTemp);
            setValueTemp('');
        }
    }
   
    
    return (
        <Box>
            <Box sx={{ pb: 7, height: "60vh", overflow: 'auto' }} ref={ref}>
                <CssBaseline />
                <List >
                    { messagesData.map((data, index) => {
                        return ( 
                        <div ref={listRef} key={data._id} >
                        { data.sender?._id !== userId ? 
                                <React.Fragment key={index + 1}>
                                    <ListItem button sx={{ textAlign: 'left' }}>
                                        <ListItemAvatar>
                                            <Avatar alt={data.name} src={"#"} />
                                        </ListItemAvatar>
                                        <ListItemText primary={data.name} secondary={data.text} />
                                    </ListItem>
                                    <ListItem>
                                    {data.file &&
                                            data.file.length ? (data.file).map((ele,i )=> {
                                                const filetyp = ele.split('.').pop()
                                                const onErrorHandler = (e) => {
                                                    console.log(e, "error in file-viewer");
                                                }
                                             return <div key={i} style={{width: '80%', position: 'relative'}}>
                                                        {/.*\.(gif|jpe?g|bmp|png)$/igm.test(ele) 
                                                        ? <img src={ele} style={{ width: "50%"}} alt="testImage" />
                                                        : <FileViewer fileType={filetyp} filePath={ele} onError={onErrorHandler} />
                                                        }
                                                        <IconButton style={{ float: 'right', marginRight: '90%' }} onClick={() => downlodeHandler(ele)}>
                                                            <DownloadIcon />
                                                        </IconButton>
                                                </div>
                                            }) : ''
                                        }
                                    </ListItem>
                                </React.Fragment>
                             :
                                <React.Fragment key={index + 1}>
                                    <ListItem button >
                                        <ListItemText primary={<Typography align='right' >
                                            {data.name}
                                        </Typography>}
                                            secondary={
                                                <Typography align='right' sx={{ color: 'gray', fontSize: 15 }}>
                                                    {data.text}
                                                </Typography>
                                            } />
                                        <ListItemAvatar sx={{ paddingLeft: 2 }} >
                                            <Avatar alt={data.name} src={'person'} />
                                        </ListItemAvatar>
                                    </ListItem>
                                    <ListItem sx={{ display: 'inline-block' }}>

                                        {data.file &&
                                            data.file.length ? (data.file).map((ele,i) => {
                                                const filetyp = ele.split('.').pop()
                                                const onErrorHandler = (e) => {
                                                    console.log(e, "error in file-viewer");
                                                }
                                             return <div key={i} style={{float: 'right'}}>
                                                        {/.*\.(gif|jpe?g|bmp|png)$/igm.test(ele) 
                                                        ? <img src={ele} style={{ width: "50%", float: "right" }} alt="testImage" />
                                                        : <FileViewer fileType={filetyp} filePath={ele} onError={onErrorHandler} />
                                                        }
                                                        <IconButton style={{ float: 'right' }} onClick={() => downlodeHandler(ele)}>
                                                            <DownloadIcon />
                                                        </IconButton>
                                                </div>
                                            }) : ''
                                        }
                                    </ListItem>
                                </React.Fragment>
                          }
                        </div>
                        )
                    })}
                </List>
            </Box>
            <Paper sx={{ p: '2px 4px', display: 'flex', alignItems: 'center' }} elevation={3}>
                <IconButton color="primary" onClick={selectFileHandler} sx={{ p: '10px' }} aria-label="directions">
                    <AttachFileIcon />
                </IconButton>
                <Divider sx={{ height: 40, m: 1 }} orientation="vertical" />

                <TextField
                    id="outlined-multiline-flexible"
                    placeholder='Type your messge here...'
                    multiline
                    fullWidth
                    maxRows={4}
                    value={valueTemp}
                    onChange={onChangeHandler}
                    onKeyDown={handleKeyDown}
                />
                <Divider sx={{ height: 40, m: 1 }} orientation="vertical" />
                <IconButton color="primary" onClick={() => { sendSocketHandler(valueTemp), setValueTemp('') }} sx={{ p: '10px' }} aria-label="directions">
                    <SendIcon />
                </IconButton>
            </Paper>

        </Box>
    );
}
